
@import url('https://fonts.googleapis.com/css2?family=Jaldi&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inika&display=swap');
/* font-family: 'Jaldi', sans-serif;
  font-family: 'Inika', serif;

*/
body {
  margin: 0;
  font-family: 'Jaldi', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.my-button{
  font-family: 'Jaldi', sans-serif;
  color: white;
  /* background-color: #ACB5BD; */
  background-color: #acb5bd;
  border-radius: 10px;
  border: 1px solid #acb5bd;
  transition: all 0.3s ease 0s;
  padding: 5px 15px 5px 15px;
  font-weight: bold;
  letter-spacing: 1px;
}

.my-button:hover{
  box-shadow: 0px 15px 20px #d0d0d0;
  transform: translateY(-7px);
}

.my-button1{
  font-family: 'Jaldi', sans-serif;
  color: white;
  /* background-color: #ACB5BD; */
  background-color: #617691;
  border-radius: 10px;
  border: 1px solid #617691;
  transition: all 0.3s ease 0s;
  padding: 5px 15px 5px 15px;
  font-weight: bold;
  letter-spacing: 1px;

}

.my-button1:hover{
  box-shadow: 0px 15px 20px #d0d0d0;
  transform: translateY(-7px);
}
.logo{
   
  font-weight: bold;
}

.logo-span1{
    color: #DF362D;
    font-size: 80px;
}
.logo-span2{
  color: #ff8300;
  font-size: 80px;
}

.nav-color{
  border-bottom: 2px solid rgba(0, 0, 0, 0.25);
  background-color: #f0f3f5;
}

.nav-links{
  font-size : 40px;
  color : #df362d;
  font-weight:  bold;
  line-height:   130px;
  margin-right:   20px;
  cursor: pointer;
}



