/* Iphone 11 pro */

@media only screen 
    and (device-width: 375px) 
    and (device-height: 812px) 
    and (-webkit-device-pixel-ratio: 3) {
      .logo-span1{
        color: #DF362D;
        font-size: 40px;
      }
      .logo-span2{
        color: #ff8300;
        font-size: 40px;
      }

      .nav-links{
        font-size : 24px;
        color : #df362d;
        font-weight:  bold;
        float: right;
        line-height: 30px;
      }

      .my-button{
        font-family: 'Jaldi', sans-serif;
        color: white;
        /* background-color: #ACB5BD; */
        background-color: #acb5bd;
        border-radius: 10px;
        border: 1px solid #acb5bd;
        padding: 2px 10px 2px 10px;
        font-weight: bold;
        letter-spacing: 1px;
      }

      
      
     }
